// 320px
// 768px
// 1200px

@mixin for-phone-up {
    @media screen and (max-width: 767px) {
      @content;
    }
  }
  @mixin for-tablet-up-min {
    @media (min-width: 768px) {
      @content;
    }
  }
  @mixin for-tablet-up {
    @media (min-width: 768px) and (max-width: 1199px) {
      @content;
    }
  }
  @mixin for-desktop {
    @media (min-width: 1200px) {
      @content;
    }
  }