  ul {
    list-style: none;
    padding-left: 0;
    margin: 0;
  }
  link {
    text-decoration: none;
    color: inherit;
  }
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  address {
    font-style: normal;
  }
  a {
    text-decoration: none;
  }
  button {
    border-width: 0;
  }